class ModelMap {
	static map = new Map();

	static setMap(key, val) {
		this.map.set(key, val);
	}

	static getMap(key) {
		if (!this.map.has(key)) return null;

		return this.map.get(key);
	}
}

export class DefaultModel {
	key = "";
	isLocal = false; // 是否是保存到本地

	constructor() {
		this.key = this.getKey();
		this.isLocal = this.getIsLocal();
	}

	setModel(val, id) {
		let Obj = ModelMap.getMap(this.key) || {};
		if (this.isLocal) {





            Obj = uni.getStorageSync(this.key) || {};

		}
		if (id !== undefined) {
			Obj[id] = val;
		} else {
			Obj = val;
		}
		if(this.isLocal) {







            uni.setStorageSync(this.key, Obj);

		}
		ModelMap.setMap(this.key, Obj);
	}

	getModel(id) {
        let obj = {};





        obj = uni.getStorageSync(this.key) || {};

		if (!this.isLocal) {
			obj = ModelMap.getMap(this.key) || {};
		}
		let val = obj[id] || null;
		if (id === undefined) {
			val = Object.keys(obj).length > 0 ? obj : null;
		}
		return val;
	}

	removeModel(key = "") {
	    if (key) {







            uni.removeStorageSync(key);

	       return;
        }

		if(this.isLocal) {







            uni.removeStorageSync(this.key);

		}
	}

	clearModel() {
        uni.clearStorageSync();
    }

	getKey() {
		return "";
	}

	getIsLocal() {
		return false;
	}
}
